&__youtube {
  $root: &;
  max-width: 480px;
  position: relative;
  margin: 0 auto;
  border: 8px solid $border-dark-gray;

  $play-button-size: 48px;
  &::before {
    @include iconTriangle($direction: right, $color:$bg-white, $width: $play-button-size, $height: ($play-button-size / 1.5));
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-35%, -50%);
    z-index: 1; }

  $space: 20px;
  &::after {
    content: "";
    width: $play-button-size + $space;
    height: $play-button-size + $space;
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border: 4px solid $border-white;
    border-radius: 50%; }

  &:hover {
    cursor: pointer; }

  & &--item {
    &.is-link {
      @include lazyImageContainer($global-image-height, $global-image-width);
      width: 100%;
      position: relative; }

    &.is-overlay {
      width: 100%;
      height: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      position: absolute;
      top: 0;
      left: 0;
      line-height: 1;
      color: rgba($font-white, 0);
      font: {
        size: $font-huge;
        family: sans-serif;
        weight: bold; }
      background-color: rgba($bg-black, 0);
      z-index: 1;
      transition: all 0.2s ease-in;

      &:hover {
        color: rgba($font-white, 1);
        background-color: rgba($bg-black, 0.7); } } }

  & &--image {
    width: 100%;
    height: auto;
    display: block; }

  @include breakpoint($max-media: tablet) {
    #{$root}--item {
      &.is-overlay {
        user-select: none; } } }

  @include breakpoint {
    #{$root}--item {
      &.is-overlay {
        font-size: $font-xx-large; } } }

  @include breakpoint($max-media: sp) {
    border: 4px solid $border-dark-gray;

    $play-button-size: 18px;
    &::before {
      @include iconTriangle($direction: right, $color:$bg-white, $width: $play-button-size, $height: ($play-button-size / 1.5)); }

    $space: 20px;
    &::after {
      width: $play-button-size + $space;
      height: $play-button-size + $space;
      border: 2px solid $border-white; }

    #{$root}--item {
      &.is-overlay {
        font-size: $font-large; } } } }
