&__copy {
  $root: &;
  position: relative;
  margin: 40px auto 0;
  text-align: center;

  &::before {
    content: "";
    width: 100%;
    height: 100%;
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    border: 2px solid $border-x-dark-gray; }

  &:first-child {
    margin-top: 0; }

  & &--item {
    $gap: 8px;
    width: 100%;
    position: relative;
    top: $gap;
    left: $gap;
    display: block;
    color: inherit;
    font: {
      size: $font-small;
      family: sans-serif; }
    line-height: 2.8;
    text-decoration: none;
    margin: 14px auto 0;
    padding: 0 0.8em;
    background-color: rgba($bg-yellow, 0.7);
    transition: all 0.4s ease;

    &::before {
      content: "";
      width: 0;
      height: 100%;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba($bg-white, 0.4);
      transition: all 0.4s ease; }

    &:first-child {
      margin-top: 0; }

    &:hover {
      text-decoration: underline;
      background-color: rgba($bg-turquoise, 0.7);

      &::before {
        width: 100%; } } }

  @include actionDesabled($root);

  @include breakpoint {
    #{$root}--item {
      font-size: $font-x-small; } } }
