&__quiz {
  $root: &;
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;

  & &--header {
    width: 100%;
    margin-bottom: 60px; }

  $image-size: 480;
  $margin-size: 80;
  & &--image {
    width: getRelativeValue($image-size, $global-contents-width-pc);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-right: getRelativeValue($margin-size, $global-contents-width-pc);
    filter: drop-shadow(0px 0px 8px rgba($bg-black, 0.7)); }

  & &--description {
    width: 100%;
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center; }

  & &--action {
    width: 100%;
    margin-top: 60px;

    &:first-child {
      margin-top: 0; } }

  & &--footer {
    &.is-timer {
      position: absolute;
      top: 30px;
      left: 30px;
      transition: 0.5s all ease; }

    &.is-hide {
      margin-top: -30px;
      opacity: 0; } }

  @include breakpoint($max-media: tablet) {
    flex-direction: column;

    #{$root}--header {
      margin-bottom: 0; }

    #{$root}--image {
      width: 100%;
      margin: 40px auto 0; }

    #{$root}--description {
      margin-top: 40px; }

    #{$root}--action {
      margin-top: 40px; }

    #{$root}--footer {
      &.is-timer {
        width: 100%;
        position: static;
        order: -1;
        margin-bottom: 60px; } } }

  @include breakpoint($max-media: phablet) {
    #{$root}--image {
      margin-top: 20px; }

    #{$root}--footer {
      &.is-timer {
        margin-bottom: 40px; } } } }
