&__information {
  $root: &;

  &.is-home {
    $cols-pc: 4;
    $col-margin-size: 40;
    $col-width: ($global-contents-width-pc - $col-margin-size * ($cols-pc - 1)) / $cols-pc;
    $vertical-prop: 4;
    $horizontal-prop: 20;

    @include base-layout(top, $root);
    overflow: hidden;
    position: relative;
    color: $font-black;
    background-color: rgba($bg-white, 1);
    box-shadow: 0 0 8px rgba($bg-black, 0.5);
    cursor: default;

    #{$root}--header {
      position: absolute;
      left: 0;
      bottom: 0.2em;
      padding: {
        left: getRelativeValue($horizontal-prop, $col-width);
        right: 5em; } }

    #{$root}--description {
      margin-top: #{$vertical-prop}px;
      padding: 0 getRelativeValue($horizontal-prop, $col-width) 2em;  // カテゴリラベル分
      transition: opacity 0.5s ease; }

    #{$root}--footer {
      &.is-category {
        width: 4.5em;
        position: absolute;
        right: 0;
        bottom: 0; } }

    @include breakpoint($max-media: sp) {
      $image-width: 160;
      $image-height: 90;
      $margin-size: 10;
      @include base-layout(left, $root, $image-width, $image-height, $margin-size);
      box-shadow: 0 0 6px rgba($bg-black, 0.5);

      #{$root}--header {
        margin-left: #{($image-width + $margin-size)}px;
        padding: {
          left: 0;
          right: 5em; } }

      #{$root}--description {
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        margin: 0;
        padding: 0.4em 0.4em 0 0; }

      #{$root}--image {
        height: auto; } } } }


