&__timer {
  $root: &;

  &.is-quiz {
    #{$root}--item {
      width: 4em;
      height: 4em;
      display: flex;
      justify-content: center;
      align-items: center;
      color: $font-x-dark-brown;
      font: {
        size: $font-huge;
        family: 'Noto Sans JP', sans-serif;
        weight: 900; }
      margin: 0 auto;
      border-radius: 50%; }

    @include breakpoint($max-media: tablet) {
      #{$root}--item {
        width: 120px;
        height: 120px;
        font-size: $font-x-large; } }

    @include breakpoint($max-media: sp) {
      #{$root}--item {
        width: 80px;
        height: 80px;
        font-size: $font-normal; } } } }
