&__part {
  $root: &;

  margin: 14px 0;

  &:first-child {
    margin-top: 0; }

  & &--item {
    font-weight: normal; }

  &.is-campaign {
    #{$root}--item {
      font-size: $font-x-large; } }

  &.is-quiz {
    margin-bottom: 40px;

    #{$root}--item {
      font: {
        size: 3.4rem;
        family: 'Noto Sans JP', sans-serif;
        weight: 900; } } } }
