&__quiz {
  $root: &;

  margin: 40px auto;

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; }

  $index-width: 2.5em;
  & &--item {
    font: {
      size: 3.4rem;
      family: 'Noto Sans JP', sans-serif;
      weight: 900; }
    padding-left: $index-width; }

  & &--label {
    &.is-index {
      width: $index-width;
      display: inline-block;
      text-align: left;
      text-indent: 0;
      margin-left: -#{$index-width}; } }

  @include breakpoint($max-media: phablet) {
    #{$root}--item {
      font-size: $font-xx-large;
      padding-left: $index-width; }

    #{$root}--label {
      &.is-index {
        width: $index-width;
        margin-left: -#{$index-width}; } } }

  @include breakpoint($max-media: sp) {
    #{$root}--item {
      font-size: $font-x-large;
      padding-left: $index-width; }

    #{$root}--label {
      &.is-index {
        width: $index-width;
        margin-left: -#{$index-width}; } } } }
