&__banner {
  $root: &;

  &.is-home {
    @include lazyImageContainer($global-image-height, $global-image-width);
    width: 100%;
    margin: #{$global-margin-pc}px 0;
    border: 8px solid $border-gray;

    &:first-child {
      margin-top: 0; }

    &:last-child {
      margin-bottom: 0; }

    #{$root}--item {
      width: 100%;
      margin-top: 20px;

      &:first-child {
        margin-top: 0; } }

    @include breakpoint {
      margin: #{$global-margin-sp}px 0; }

    @include breakpoint($max-media: sp) {
      border: 4px solid $border-gray; } } }
