.u-link {
  &__blank {
    &::after {
      $icon-size: 1em;
      content: '';
      width: $icon-size;
      height: $icon-size;
      display: inline-block;
      position: relative;
      top: 0.1em;
      margin-left: 0.2em;
      background: url(/nankyoku/assets/images/window_01.svg) no-repeat center center;
      background-size: cover; } }

  &__forward {
    &::after {
      @include iconTriangle(right, $bg-x-dark-gray, 8px, 4px);
      margin: -0.3em 0 0 0.3em; } }

  &__backward {
    &::before {
      @include iconTriangle(left, $bg-x-dark-gray, 8px, 4px);
      margin: -0.3em 0.3em 0 0; } }

  &__jump {
    &::before {
      @include iconTriangle(down, $bg-x-dark-gray, 8px, 4px);
      margin: -0.3em 0.3em 0 0; } }

  &__emphasis {
    color: $font-red; }

  &__block {
    display: block; }

  &__black {
    color: $font-black;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } }

  &__white {
    color: $font-white;
    text-decoration: none;

    &:hover {
      text-decoration: underline; } }

  &__tooltip {
    position: relative;

    &::before,
    &::after {
      display: block;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      opacity: 0;
      transition: opacity 0.5s ease, bottom 0.3s ease; }

    $triangle-width: 8px;
    $triangle-height: 6px;
    $tip-position: -4px;
    &::before {
      content: '';
      width: 0;
      height: 0;
      bottom: 0;
      border: {
        style: solid;
        width: 0 ($triangle-width / 2) $triangle-height ($triangle-width / 2);
        color: transparent transparent $bg-x-dark-gray transparent; } }

    $font-size: 10px;
    $prop: $font-size * 0.6;
    $tip-height: $font-size + $prop * 2;
    &::after {
      height: $tip-height;
      content: attr(data-tooltip);
      bottom: -($tip-height);
      overflow: hidden;
      line-height: 1;
      color: $font-white;
      font: {
        size: $font-xx-small;
        family: sans-serif;
        weight: normal; }
      padding: $prop 1em;
      background-color: rgba($bg-x-dark-gray, 1);
      border-radius: 5px;
      white-space: nowrap;
      box-sizing: border-box; }

    &:hover {
      &::before,
      &::after {
        opacity: 1; }

      &::before {
        bottom: ($tip-position - $triangle-height); }

      &::after {
        bottom: $tip-position - $triangle-height - $tip-height; } } } }
