&__chatTypeSelector {
  $root: &;

  & &--item {
    width: 100%;
    display: block;
    position: relative;
    overflow: hidden;
    color: $font-white;
    font-size: $font-small;
    padding: 0.8em 1em;
    border-radius: 5px;
    transition: all 0.2s ease;

    &::before {
      content: "";
      width: 100%;
      height: 4px;
      display: block;
      position: absolute;
      bottom: 0;
      left: 0; }

    &:not(:disabled):hover {
      opacity: 0.5; }

    &:disabled {
      color: inherit; } }

  & &--icon {
    width: 3.5em;
    height: 1em;
    display: inline-block;
    position: relative;
    line-height: 1;
    font-size: $font-x-small;
    margin-right: 0.2em;
    padding: 0.4em 0.2em;
    background-color: rgba($bg-white, 1);
    border-radius: 1em;
    box-sizing: content-box; }

  &.is-none,
  &.is-pt,
  &.is-alliance {
    #{$root}--item {
      &:disabled {
        background-color: transparent;
        box-shadow: 0 -4px 8px -2px rgba($bg-black, 0.2);

        &::before {
          background-color: transparent; } } } }

  &.is-none {
    #{$root}--item {
      background-color: rgba($bg-light-green, 1);

      &::before {
        background-color: rgba($bg-dark-green, 1); } }

    #{$root}--icon {
      color: $bg-light-green; } }

  &.is-pt {
    #{$root}--item {
      background-color: rgba($bg-light-blue, 1);

      &::before {
        background-color: rgba($bg-dark-blue, 1); } }

    #{$root}--icon {
      color: $bg-light-blue; } }

  &.is-alliance {
    #{$root}--item {
      background-color: rgba($bg-light-red, 1);

      &::before {
        background-color: rgba($bg-dark-red, 1); } }

    #{$root}--icon {
      color: $bg-light-red; } }

  @include breakpoint {
    #{$root}--item {
      font-size: $font-x-small;
      padding: 0.8em 0.5em; }

    #{$root}--icon {
      font-size: $font-xx-small;
      padding: 0.2em; } }

  @include breakpoint($max-media: sp) {
    &.is-none,
    &.is-pt,
    &.is-alliance {
      #{$root}--item {
        &:disabled {
          box-shadow: 8px 0 6px rgba($bg-black, 0.2); } } } } }

