&__toastNotice {
  $root: &;
  $toast-height: 4em;

  width: 100%;
  position: fixed;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
  display: flex;
  flex-direction: column-reverse;
  z-index: 21;

  & &--row {
    width: 100%;
    height: 0;
    position: relative;
    top: 0;
    opacity: 0;
    overflow: hidden;
    box-shadow: 0px 2px 6px rgba($bg-black, 0.8); }

  @keyframes toastMessage {
    0% {
      height: 0;
      opacity: 0;
      left: 0; }

    15% {
      height: $toast-height;
      left: 0;
      opacity: 1.0; }

    80% {
      height: $toast-height;
      left: 0;
      opacity: 1.0; }

    100% {
      height: 0;
      left: 0;
      opacity: 0; } } }
