&__globalNavigation {
  $root: &;

  &.is-drawer {
    text-align: center;

    #{$root}--item {
      color: rgba($font-white, 1);
      font: {
        size: $font-normal;
        family: sans-serif; }
      letter-spacing: 0.1em;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } } }

  &.is-header {
    height: 100%;
    display: flex;
    align-items: center;

    #{$root}--item {
      color: rgba($font-white, 1);
      font-size: $font-small;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } } }

  &.is-footer {
    #{$root}--item {
      display: block;
      color: $font-white;
      font: {
        size: $font-small;
        family: sans-serif; }
      text-align: center;
      text-decoration: none;

      &:hover {
        text-decoration: underline; } }

    @include breakpoint {
      #{$root}--item {
        display: inline;
        font-size: $font-x-small; } } } }
