&__done {
  $root: &;

  &.is-quiz {
    width: 100%;
    position: absolute;
    top: 50%;
    left: 50%;
    margin: 0 auto;
    animation: {
      name: scaleIn__quizComplete;
      fill-mode: forwards;
      duration: 1s;
      timing-function: cubic-bezier(.9, -0.01, 1, .35); }

    #{$root}--item {
      width: 100%; } }

  @keyframes scaleIn__quizComplete {
    0% {
      opacity: 0;
      filter: blur(6px);
      transform: translate(-50%, -50%) scale(3); }

    100% {
      opacity: 1;
      filter: blur(0);
      transform: translate(-50%, -50%) scale(1); } } }
