&__share {
  $root: &;

  &.is-global {
    #{$root}--unit {
      display: flex;
      flex-direction: column;
      align-items: center; }

    #{$root}--term {
      color: $font-white;
      font: {
        size: $font-xx-small;
        weight: bold;
        family: sans-serif; }
      writing-mode: vertical-rl;
      text-shadow: 0 0 4px rgba($bg-black, 0.2);
      cursor: default;
 }      // transform: rotate(90deg)

    #{$root}--description {
      margin-top: 20px; } }

  &.is-quiz {
    margin-top: 80px;

    &:first-child {
      margin-top: 0; }

    #{$root}--items {
      display: flex;
      justify-content: center;
      align-items: center; }

    $cols: 3;
    $margin-size: 40;
    $button-width: ($global-contents-width-pc - $margin-size * ($cols - 1)) / $cols;
    #{$root}--item {
      width: getRelativeValue($button-width, $global-contents-width-pc);
      margin-left: getRelativeValue($margin-size, $global-contents-width-pc);

      &:first-child {
        margin-left: 0; } }

    @include breakpoint($max-media: sp) {
      margin-top: 40px; } } }
