&__toggle {
  $root: &;

  &.is-questionAndAnswer__tools {
    position: relative;
    z-index: 1;

    #{$root}--item {
      width: 100%;
      display: block;
      position: relative;

      $button-size: 20px;
      $arrow-size: $button-size - 14;
      &::before {
        content: "";
        width: $arrow-size;
        height: $arrow-size;
        display: block;
        position: absolute;
        right: (10px + ($button-size - $arrow-size) / 2);
        top: 50%;
        border: {
          bottom: 2px solid $border-white;
          right: 2px solid $border-white; }
        transition: all 0.5s ease; }

      &.is-close {
        &::before {
          transform: translateY(-70%) rotate(45deg); } }

      &.is-open {
        &::before {
          transform: translateY(-30%) rotate(225deg); } }

      &::after {
        content: "";
        width: $button-size;
        height: $button-size;
        display: block;
        position: absolute;
        right: 10px;
        top: 50%;
        transform: translateY(-50%);
        border: 1px solid $border-white;
        border-radius: 5px; } }

    &:hover {
      text-decoration: underline; } }

  &.is-footer {
    position: absolute;
    right: 10px;
    top: 50%;
    transform: translateY(-50%);
    line-height: 1;

    $button-size: 2em;
    #{$root}--item {
      width: $button-size;
      height: $button-size;
      overflow: hidden;
      position: relative;
      line-height: 1;
      color: rgba($font-white, 0);
      border: 1px solid $border-gray;
      border-radius: $border-radius;

      $line-length: ($button-size * 0.4);
      $line-weight: 2px;
      &::before,
      &::after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-color: rgba($bg-white, 1); }

      &::before {
        width: $line-length;
        height: $line-weight;
        transition: opacity 0.5s ease, transform 0.5s ease; }

      &::after {
        width: $line-weight;
        height: $line-length;
        transition: transform 0.5s ease; } }

    &.is-open {
      #{$root}--item {
        &::before,
        &::after {
          transform: translate(-50%, -50%) rotate(270deg); }

        &::before {
          opacity: 0; } } }

    @include breakpoint($max: false, $min: true, $min-media: phablet) {
      display: none; } } }
