&__chatTypeSelector {
  $root: &;

  margin-top: #{$global-margin-pc}px;

  &:first-child {
    margin-top: 0; }

  & &--items {
    width: 100%;
    display: flex;
    align-items: center; }

  & &--item {
    flex: 1;
    margin-left: 20px;

    &:first-child {
      margin-left: 0; } }

  @include breakpoint($max-media: sp) {
    margin-top: #{$global-margin-sp}px;

    #{$root}--items {
      display: block; }

    #{$root}--item {
      margin: 10px 0 0 0;

      &:first-child {
        margin-top: 0; } } } }
