&__macro {
  $root: &;
  width: 100%;
  display: flex;

  & &--description {
    flex: 1; }

  & &--action {
    width: 9em;
    margin-left: 20px; }

  @include breakpoint {
    #{$root}--action {
      width: 7em; } }

  @include breakpoint($max-media: sp) {
    #{$root}--action {
      width: 5em; } } }
