&__tools {
  $root: &;
  overflow-x: auto;
  margin-top: #{$global-margin-pc}px;
  padding-bottom: 40px;
  background-color: rgba($bg-xx-dark-gray, 1);
  border-radius: $border-radius;
  box-shadow: 0 0 20px rgba($bg-black, 0.7);

  &:first-child {
    margin-top: 0; }

  & &--unit {
    width: 100%;
    margin-top: 12px;
    border: none;
    border-collapse: collapse;
    border-spacing: 0;

    &:first-child {
      margin-top: 0; } }

  & &--colgroup,
  & &--col {
    @for $i from 1 through 19 {
      // \0025 = 「%」
      // is-5% 〜 is-95% (5% 刻み)
      &.is-#{$i * 5}\0025 {
        width: $i * 5%; } } }

  & &--caption {
    font-size: $font-xx-large;
    padding: 1.8em 0 0.8em; }

  & &--header {
    background-color: rgba($bg-black, 0.5);

    #{$root}--head {
      font-size: $font-small;

      &.is-fixed {
        position: sticky;
        left: 0;
        z-index: 1; } } }

  & &--body {
    #{$root}--head {
      position: sticky;
      left: 0;
      z-index: 1; } }

  & &--row {
    &.is-even {
      #{$root}--head {
        background-color: rgba($bg-xx-dark-gray, 1); }

      #{$root}--head,
      #{$root}--data {
        &::before {
          background: linear-gradient(rgba($bg-black, 0.4), rgba($bg-black, 0)); } } }

    &.is-odd {
      #{$root}--head {
        background-color: rgba($bg-x-dark-gray, 1); }

      #{$root}--head,
      #{$root}--data {
        &::before {
          background: linear-gradient(rgba($bg-black, 0.8), rgba($bg-black, 0)); } } }

    &:first-child {
      #{$root}--head,
      #{$root}--data {
        &::before {
          display: none; } } } }

  & &--head,
  & &--data {
    position: relative;
    text-align: center;
    padding: 0.5em;
    border: none;

    &::before {
      content: "";
      width: 100%;
      height: 6px;
      display: block;
      position: absolute;
      left: 0;
      top: 0; } }

  @include breakpoint {
    #{$root}--caption {
      font-size: $font-x-large; } }

  // 制圧戦
  &.is-secure {
    #{$root}--row {
      &.is-even {
        #{$root}--data {
          background-color: rgba($bg-x-dark-red, 1); } }

      &.is-odd {
        #{$root}--data {
          background-color: rgba($bg-dark-red, 1); } } } }

  // シールロック
  &.is-seize {
    #{$root}--row {
      &.is-even {
        #{$root}--data {
          background-color: rgba($bg-x-dark-yellow, 1); } }

      &.is-odd {
        #{$root}--data {
          background-color: rgba($bg-dark-yellow, 1); } } } }

  // 砕氷戦
  &.is-shatter {
    #{$root}--row {
      &.is-even {
        #{$root}--data {
          background-color: rgba($bg-x-dark-blue, 1);

          &.is-active {
            background-color: rgba($bg-x-dark-yellow, 1); } } }

      &.is-odd {
        #{$root}--data {
          background-color: rgba($bg-dark-blue, 1);

          &.is-active {
            background-color: rgba($bg-dark-yellow, 1); } } } }

    &.is-popPattern {
      #{$root}--unit {
        min-width: 700px; }

      #{$root}--header {
        #{$root}--head {
          background-color: rgba($bg-x-dark-blue, 1);

          &.is-pattern {
            background-color: rgba($bg-black, 1); }

          &.is-active {
            color: $font-black;
            background-color: rgba($bg-yellow, 1); }

          &.is-small,
          &.is-large {
            background-color: rgba($bg-dark-gray, 1); }

          &.is-large {
            &::after {
              content: "";
              width: 1px;
              height: 100%;
              display: block;
              position: absolute;
              top: 0;
              right: 0;
              background-color: rgba($border-black, 0.5); }

            &:last-child {
              &::after {
                display: none; } } } }

        #{$root}--row:first-child {
          #{$root}--head:first-child {
            background-color: rgba($bg-x-dark-blue, 1); } } }

      #{$root}--data {
        &:nth-child(2n+1) {
          &::after {
            content: "";
            width: 1px;
            height: 100%;
            display: block;
            position: absolute;
            top: 0;
            right: 0;
            background-color: rgba($border-black, 0.5); }

          &:last-child {
            &::after {
              display: none; } } } } } }

  // オンサル・ハカイル
  &.is-danshigNaadam {
    #{$root}--row {
      &.is-even {
        #{$root}--data {
          background-color: rgba($bg-x-dark-green, 1); } }

      &.is-odd {
        #{$root}--data {
          background-color: rgba($bg-dark-green, 1); } } } } }

