&__quiz {
  $root: &;

  &.is-start,
  &.is-next {
    text-align: center;
    margin-top: 60px;

    &:first-child {
      margin-top: 0; }

    #{$root}--item {
      line-height: 1;
      font: {
        size: $font-huge;
        family: 'Noto Sans JP', sans-serif;
        weight: 900; }
      padding: {
        left: 0.1em;
        right: 0.1em;
        bottom: 0.5em; }
      border-bottom: 2px solid $border-x-dark-brown;
      transition: 0.5s all ease;

      &:hover {
        color: rgba($font-black, 0.5);
        padding: {
          left: 1em;
          right: 1em; }
        border-bottom: 2px solid $border-brown; } } }

  @include breakpoint($max-media: sp) {
    &.is-start,
    &.is-next {
      margin-top: 60px;

      #{$root}--item {
        line-height: 1;
        font-size: $font-xx-large; } } } }
