&__score {
  $root: &;

  &.is-quiz {
    min-width: 5em;
    min-height: $font-xx-huge;
    position: relative;
    line-height: 1;
    color: $font-red;
    font: {
      size: $font-xx-large;
      family: 'Noto Sans JP', sans-serif;
      weight: 900; }
    margin: 40px auto 0;
    padding: {
      left: 5em;
      right: 1em;
      bottom: 0.2em; }
    border-bottom: 1px solid $border-x-dark-brown;

    &:first-child {
      margin-top: 0; }

    &::before {
      content: "合計点";
      position: absolute;
      bottom: 0.8em;
      left: 1em; }

    #{$root}--item {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      align-items: flex-end;
      text-align: center;
      padding-bottom: 0.5em;
      opacity: 0;
      animation: {
        name: scaleIn__totalScore;
        fill-mode: both;
        duration: 1s;
        timing-function: cubic-bezier(.9, -0.01, 1, .35); } }

    #{$root}--label {
      &.is-score {
        font-size: $font-xx-huge; }

      &.is-total {
        font-size: $font-xx-large;
        margin-left: 0.2em; }

      &.is-notUnique {
        font-size: $font-xx-large;
        margin-left: 0.2em; } }

    @include breakpoint($max-media: tablet) {
      min-height: $font-x-huge;
      font-size: $font-large;

      #{$root}--label {
        &.is-score {
          font-size: $font-x-huge; }

        &.is-total {
          font-size: $font-x-large; }

        &.is-notUnique {
          font-size: $font-x-large; } } }

    @include breakpoint($max-media: sp) {
      min-height: $font-xx-large;
      font-size: $font-small;

      #{$root}--label {
        &.is-score {
          font-size: $font-xx-large; }

        &.is-total {
          font-size: $font-small; }

        &.is-notUnique {
          font-size: $font-small; } } } }

  @keyframes scaleIn__totalScore {
    0% {
      opacity: 0;
      filter: blur(6px);
      transform: scale(6); }

    100% {
      opacity: 1;
      filter: blur(0);
      transform: scale(1); } } }
