&__about {
  $root: &;
  $image-width: 370px;
  $image-height: 485px;
  @include lazyImageContainer($image-height, $image-width);
  margin: 80px 0;
  box-shadow: 40px 40px rgba($bg-black, 0.2);

  &:first-child {
    margin-top: 0; }

  &:last-child {
    margin-bottom: 0; }

  & &--item {
    &[data-is-liquid="true"] {
      width: 100%;
      height: auto;
      vertical-align: bottom; } }

  @include breakpoint($max-media: pc) {
    box-shadow: 20px 20px rgba($bg-black, 0.2); }

  @include breakpoint($max-media: sp) {
    box-shadow: none; } }
