&__scroll {
  $root: &;

  &.is-quiz {
    $button-size: 180;
    $screen-height: 960;

    #{$root}--item {
      width: #{$button-size / $screen-height * 100}vmin;
      height: #{$button-size / $screen-height * 100}vmin;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 20px;
      border: 2px solid $border-white;
      border-radius: 50%;
      transition: all 1s ease;
      background-color: rgba($bg-white, 0);
      animation: {
        name: crossFade;
        fill-mode: backwards;
        duration: 3s;
        iteration-count: infinite;
        timing-function: ease; }

      &:hover {
        animation-iteration-count: 1;
        background-color: rgba($bg-white, 0.2); } }

    @include breakpoint($max-media: tablet) {
      $button-size: 320;
      $screen-height: 1180;
      #{$root}--item {
        width: #{$button-size / $screen-height * 100}vmin;
        height: #{$button-size / $screen-height * 100}vmin; } }

    @include breakpoint($max-media: phablet) {
      $button-size: 280;
      $screen-height: 1024;
      #{$root}--item {
        width: #{$button-size / $screen-height * 100}vmin;
        height: #{$button-size / $screen-height * 100}vmin; } }

    @include breakpoint($max-media: sp) {
      $button-size: 320;
      $screen-height: 851;
      #{$root}--item {
        width: #{$button-size / $screen-height * 100}vmin;
        height: #{$button-size / $screen-height * 100}vmin; } } } }

@keyframes crossFade {
  0% {
    opacity: 0; }

  50% {
    opacity: 1; }

  100% {
    opacity: 0; } }
