&__rule {
  $root: &;

  &.is-quiz {
    max-width: 660px;
    margin: 20px auto 0;
    padding: 3em;
    border: 1px solid $border-brown;

    &:first-child {
      margin-top: 0; }

    $bullet-size: 0.3em;
    $margin-size: 0.6em;
    #{$root}--item {
      position: relative;
      font-size: $font-small;
      text-indent: -($bullet-size + $margin-size);
      margin-top: 1em;
      padding-left: ($bullet-size + $margin-size);

      &:first-child {
        margin-top: 0; }

      &::before {
        content: ' ';
        width: $bullet-size;
        height: $bullet-size;
        display: inline-block;
        text-align: left;
        text-indent: 0;
        vertical-align: middle;
        margin: {
          top: -0.2em;
          right: $margin-size; }
        background-color: rgba($bg-black, 1);
        border-radius: 50%;
        box-sizing: border-box; } }

    @include breakpoint($max-media: sp) {
      padding: 1.5em; } } }
