&__quiz {
  $root: &;

  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  margin: 40px auto 0;

  &:first-child {
    margin-top: 0; }

  $image-size: 480;
  & &--col {
    &.is-image {
      width: getRelativeValue($image-size, $global-contents-width-pc);
      margin-right: 80px;
      filter: drop-shadow(0px 0px 8px rgba($bg-black, 0.7)); }

    &.is-description {
      width: 100%;
      flex: 1;
      display: flex;
      flex-direction: column;
      justify-content: center;
      position: relative;
      padding: 3em 1em 1em;
      background-color: rgba($bg-xx-light-gray, 1);

      &::before {
        content: "回答";
        position: absolute;
        top: 0;
        left: 0;
        color: $font-black;
        padding: 0.3em 1em;
        background-color: rgba($bg-light-gray, 1); } }

    &.is-answer,
    &.is-modelAnswer {
      width: 100%;
      position: relative;
      margin-top: 20px;
      padding: 3em 1em 1em;
      background-color: rgba($bg-xx-light-brown, 1);

      &::before {
        position: absolute;
        top: 0;
        left: 0;
        color: $font-white;
        padding: 0.3em 1em;
        background-color: rgba($bg-brown, 1); } }

    &.is-answer {
      &::before {
        content: "正解"; } }

    &.is-modelAnswer {
      &::before {
        content: "模範解答"; } } }

  @include breakpoint($max-media: tablet) {
    flex-direction: column;
    flex-wrap: wrap;

    #{$root}--col {
      &.is-image {
        width: 100%;
        margin: 40px auto 0;

        &:first-child {
          margin-top: 0; } }

      &.is-description {
        margin-top: 40px;

        &:first-child {
          margin-top: 0; } } } } }

