/**
 * フェードイン / アウト
 */
.is-fade-enter-active,
.is-fade-leave-active {
  transition: all 0.3s ease; }

.is-fade-enter,
.is-fade-leave-to {
  opacity: 0; }

/**
 * スライドイン / アウト
 */
.is-slide-enter-active,
.is-slide-leave-active {
  transition: all 0.3s ease; }

.is-slide-enter,
.is-slide-leave-to {
  transform: translateX(-100vw); }

/**
 * スライドイン / アウト（左右）
 */
.is-slide__horizontal-enter-active,
.is-slide__horizontal-leave-active {
  transition: all 0.5s ease; }

.is-slide__horizontal-enter,
.is-slide__horizontal-leave-to {
  transform: translateX(100vw); }
  @media screen and (max-width: 1024px) {
    .is-slide__horizontal-enter,
    .is-slide__horizontal-leave-to {
      transform: translateX(-100vw); } }

/**
 * スライドイン / アウト（上下）
 */
.is-slide__vertical-enter-active,
.is-slide__vertical-leave-active {
  transition: all 0.5s ease; }

.is-slide__vertical-enter,
.is-slide__vertical-leave-to {
  opacity: 0;
  transform: translateY(-30vh); }

/**
 * ポップアップ
 */
.is-pop-enter-active,
.is-pop-leave-active {
  transition: all 0.3s ease; }

.is-pop-enter {
  transform: translateY(24px); }

.is-pop-leave-to {
  transform: translateY(-24px); }

/**
 * トーストメッセージ
 */
.is-toast-enter-active {
  animation: toastMessage 3s forwards; }

/**
 * ripple effect
 */
.is-ripple-enter-active {
  position: absolute;
  top: 50%;
  left: 50%;
  animation: ripple 0.8s ease-out; }

@keyframes ripple {
  from {
    transform: scale(1) translate(-50%, -50%);
    opacity: 1; }
  to {
    transform: scale(2) translate(-25%, -25%);
    opacity: 0; } }

.is-ripple-leave {
  display: none; }

.is-lazyload,
.img.is-loading {
  max-width: 128px; }

.is-lazyloaded {
  animation: fadeIn 0.5s linear; }

@keyframes fadeIn {
  0% {
    opacity: 0; }
  100% {
    opacity: 1; } }

