&__toastNotice {
  $root: &;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  & &--item {
    line-height: 1;
    color: $font-white;
    white-space: nowrap; }  // css アニメーションで高さを固定値で使う必要があるため、改行を禁止

  &.is-completed {
    background-color: rgba($bg-turquoise, 1.0); }

  &.is-failed {
    background-color: rgba($bg-dark-red, 1.0); } }
