&__page {
  $root: &;
  width: 100%;
  max-width: #{$global-contents-width-pc}px;
  margin: 0 auto;

  #{$root}--item {
    font-weight: normal;
    font-size: $font-huge; }

  @include breakpoint {
    #{$root}--item {
      font-size: $font-xx-large; } }

  &.is-about {
    margin-bottom: 0;
    padding-bottom: 100px;

    #{$root}--item {
      display: inline-block;
      position: relative;
      line-height: 1;
      color: rgba($font-white, 1);
      font-family: serif;
      text-shadow: 0 0 1em rgba($font-black, 1);
      padding-bottom: #{$global-section-vertical-prop-pc}px;

      &::before {
        content: "";
        width: 80px;
        height: 1px;
        display: block;
        position: absolute;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        background-color: rgba($bg-white, 1); } }

    #{$root}--label {
      line-height: 1;

      &.is-primary {
        font-size: $font-xx-huge; }

      &.is-secondary {
        font-size: $font-xx-large;
        font-weight: bold;
        margin-left: 1.5em; } }

    @include breakpoint($max-media: tablet) {
      width: 100%;
      padding-bottom: 60px;

      #{$root}--item {
        display: block;
        text-align: center;
        padding-bottom: #{$global-section-vertical-prop-sp}px; }

      #{$root}--label {
        &.is-primary {
          font-size: $font-x-huge; }

        &.is-secondary {
          font-size: $font-x-large; } } }

    @include breakpoint($max-media: sp) {
      padding-bottom: 40px;

      #{$root}--label {
        display: block;

        &.is-primary {
          font-size: $font-x-huge; }

        &.is-secondary {
          font-size: $font-normal;
          margin: {
            top: 0.8em;
            left: 0; } } } } }

  &.is-quiz {
    $heading-width: 994;
    width: 100%;
    height: 100%;
    max-width: #{$heading-width}px;
    position: relative;
    padding-bottom: 40px;

    #{$root}--item {
      height: 100%;
      color: rgba($font-black, 0);
      font-size: 0;
      background: {
        image: url(/nankyoku/assets/images/logo_quiz_01.png);
        repeat: no-repeat;
        position: center bottom;
        size: contain; } }

    @include breakpoint($max-media: tablet) {
      #{$root}--item {
        background-position: top; } } } }
