&__hero {
  $root: &;

  &.is-quiz {
    $hero-height: 960;
    $logo-height: 465;
    height: 100%;
    display: flex;
    flex-direction: column;
    position: relative;

    &::before {
      content: "";
      width: 100%;
      max-width: 908px;
      height: getRelativeValue(400, $hero-height);
      display: block;
      position: absolute;
      top: getRelativeValue(280, $hero-height);
      left: 50%;
      transform: translateX(-50%);
      background: {
        image: url(/nankyoku/assets/images/pic_quiz_01.png);
        repeat: no-repeat;
        position: center top;
        size: contain; }
      z-index: 1; }

    #{$root}--row {
      &.is-logo {
        height: getRelativeValue($logo-height, $hero-height);
        order: 1;
        position: relative;
        z-index: 2;
        animation: {
          name: scaleIn__quizLogo;
          fill-mode: forwards;
          duration: 2s;
          timing-function: ease; } }

      &.is-category {
        width: 100%;
        max-width: 840px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 2; }

      &.is-caption {
        height: getRelativeValue($hero-height - $logo-height, $hero-height);
        position: relative;
        animation: {
          name: bounds__quizLogo;
          fill-mode: forwards;
          duration: 1s;
          timing-function: ease-out; } }

      &.is-action {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        z-index: 2;
        animation: {
          name: fadeIn__quizLogo;
          fill-mode: forwards;
          duration: 4s;
          timing-function: ease; } }

      &.is-noanimation {
        animation-name: none; } }

    @include breakpoint($max-media: sp) {
      $hero-height: 667;
      $logo-height: 380;
      &::before {
        top: getRelativeValue(280, $hero-height);
        transform: translate(-50%, -50%);
        background-position: center center; }

      #{$root}--row {
        &.is-logo {
          height: getRelativeValue($logo-height, $hero-height); }

        &.is-category {
          top: getRelativeValue(280, $hero-height); }

        &.is-caption {
          height: getRelativeValue($hero-height - $logo-height, $hero-height); }

        &.is-action {
          top: inherit;
          bottom: 10%;
          transform: translateX(-50%); } } } }

  @keyframes bounds__quizLogo {
    0% {
      top: -300px;
      transform: scale(2);
      z-index: 1; }

    30% {
      top: 300px;
      z-index: 1; }

    60% {
      top: -100px;
      z-index: 1; }

    90% {
      top: 120px;
      z-index: 1; }

    100% {
      top: 0;
      z-index: 0; } }

  @keyframes scaleIn__quizLogo {
    0% {
      filter: blur(10px);
      transform: scale(6);
      opacity: 0; }

    49% {
      filter: blur(10px);
      transform: scale(6);
      opacity: 0; }

    50% {
      filter: blur(10px);
      transform: scale(6);
      opacity: 1; }

    100% {
      filter: blur(0);
      transform: scale(1); } }

  @keyframes fadeIn__quizLogo {
    0% {
      opacity: 0; }

    64% {
      opacity: 0; }

    65% {
      opacity: 1; }

    100% {
      opacity: 1; } } }

