&__share {
  $root: &;

  & &--item {
    display: block; }

  &.is-quiz {
    #{$root}--item {
      text-align: center;
      padding: 10px;
      background-color: rgba($bg-black, 1);
      border-radius: $border-radius;
      opacity: 1;
      transition: all 0.2s ease;

      &:hover {
        opacity: 0.5; } }

    @include breakpoint($max-media: sp) {
      #{$root}--item {
        padding: 6px; } } } }
