@import 'sprite';

// 2x images for Retina
@mixin sprite-width2x($sprite) {
  width: (nth($sprite, 5) / 2); }

@mixin sprite-height2x($sprite) {
  height: (nth($sprite, 6) / 2); }

@mixin sprite-position2x($sprite) {
  $sprite-offset-x: (nth($sprite, 3) / 2);
  $sprite-offset-y: (nth($sprite, 4) / 2);
  background-position: $sprite-offset-x  $sprite-offset-y; }

@mixin sprite-size2x($sprite) {
  background-size: (nth($sprite, 7) / 2) (nth($sprite, 8) / 2); }

@mixin sprite2x($sprite) {
  @include sprite-image($sprite);
  @include sprite-position2x($sprite);
  @include sprite-width2x($sprite);
  @include sprite-height2x($sprite);
  @include sprite-size2x($sprite); }

@mixin sprites2x($sprites) {
  @each $sprite in $sprites {
    $sprite-name: nth($sprite, 10);
    .#{$sprite-name} {
      @include sprite2x($sprite); } } }

$follow_services: twitter, youtube;
$share_services: twitter, facebook, line, mail;

@mixin iconDefaultStyle($key) {
  @include sprite2x($key);
  display: inline-block;
  vertical-align: middle; }

@mixin svgIconDefaultStyle() {
  display: inline-block;
  vertical-align: middle;
  background: {
    size: contain;
    position: center center;
    repeat: no-repeat; } }

// share SNS
@each $service in $share_services {
  .i-share {
    &__#{$service} {
      @include svgIconDefaultStyle;

      &.is-quiz {
        width: 30px;
        height: 30px;
        background-image: url(/nankyoku/assets/images/#{$service}_white.svg);

        @include breakpoint($max-media: sp) {
          width: 20px;
          height: 20px; } }

      &.is-global {
        width: 16px;
        height: 16px;
        background-image: url(/nankyoku/assets/images/#{$service}_white.svg);
        opacity: 1;
        transition: opacity 0.5s ease;

        &:hover {
          opacity: 0.5; } } } } }

@each $service in $follow_services {
  .i-follow {
    &__#{$service} {
      @include svgIconDefaultStyle;

      &.is-profile {
        width: 16px;
        height: 16px;
        background-image: url(/nankyoku/assets/images/#{$service}_navy.svg); }

      &.is-header {
        width: 24px;
        height: 24px;
        background-image: url(/nankyoku/assets/images/#{$service}_white.svg); }

      &.is-footer {
        width: 32px;
        height: 32px;
        background-image: url(/nankyoku/assets/images/#{$service}_black.svg); }

      &.is-drawerNavigation {
        width: 32px;
        height: 32px;
        background-image: url(/nankyoku/assets/images/#{$service}_white.svg); } } } }

.i-scale.is-carousel {
  @include iconDefaultStyle($scale_carousel);
  background-color: rgba($bg-black, 0.5);
  border-radius: 50%;
  transition: background-color 0.5s ease;

  &:hover {
    background-color: rgba($bg-black, 0.8); } }

.i-copy.is-black {
  @include svgIconDefaultStyle;
  width: 0.8em;
  height: 0.8em;
  vertical-align: baseline;
  background-image: url(/nankyoku/assets/images/copy_black.svg); }
