&__globalNavigation {
  $root: &;

  &.is-drawer {
    #{$root}--item {
      margin-top: 40px;

      &:first-child {
        margin-top: 0; } }

    @include breakpoint {
      #{$root}--item {
        margin-top: 30px; } } }

  &.is-header {
    height: 100%;

    #{$root}--items {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: space-around; }

    #{$root}--item {
      height: 100%; } }

  &.is-footer {
    width: 100%;
    height: 100%;

    #{$root}--items {
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center; }

    #{$root}--item {
      height: 100%;
      margin-left: getRelativeValue(100, $global-contents-width-pc);

      &:first-child {
        margin-left: 0; } } } }

