&__category {
  $root: &;

  &.is-information {
    padding: 0.1em;
    #{$root}--item {
      color: $font-white;
      font: {
        size: $font-x-small;
        weight: bold;
        family: sans-serif; }
      text-align: center; }

    &.is-release {
      background-color: rgba($bg-dark-blue, 1); }

    &.is-notice {
      background-color: rgba($bg-dark-red, 1); }

    &.is-event {
      background-color: rgba($bg-dark-green, 1); }

    @include breakpoint($max-media: sp) {
      #{$root}--item {
        font-size: $font-xx-small; } } }

  &.is-quiz {
    position: relative;
    padding: 0 40px;
    animation: {
      name: fadeIn__quizCategory;
      fill-mode: forwards;
      duration: 4s;
      timing-function: ease; }

    #{$root}--item {
      color: $font-white;
      text-align: right;
      font: {
        size: $font-xx-large;
        family: 'Noto Sans JP', sans-serif;
        weight: 900; } }

    @include breakpoint($max-media: phablet) {
      #{$root}--item {
        font: {
          size: $font-large;
          weight: 400; } } }

    @keyframes fadeIn__quizCategory {
      0% {
        transform: translateX(-60px);
        opacity: 0;
        filter: blur(10px); }

      50% {
        transform: translateX(-60px);
        opacity: 0;
        filter: blur(10px); }

      80% {
        transform: translateX(0);
        opacity: 1;
        filter: blur(0); }

      100% {
        transform: translateX(0);
        opacity: 1;
        filter: blur(0); } } } }

