&__loading {
  $root: &;
  margin-top: 6px;

  &:first-child {
    margin-top: 0; }

  & &--item {
    color: #fff;
    font: {
      size: $font-huge;
      family: 'Noto Sans JP', sans-serif;
      weight: 900; }
    text-align: center; }

  @include breakpoint($max-media: sp) {
    #{$root}--item {
      font-size: $font-xx-large; } } }
