&__score {
  $root: &;

  &.is-quiz {
    width: 100%;
    max-width: 720px;
    margin: 0 auto;

    #{$root}--unit {
      position: relative;
      top: 20px;
      padding-top: 40px; // スクロールイベントでスクロールした際に上に余白ほしいので、margin ではなく padding で対処
      opacity: 0;
      transition: 1s all ease;

      &:first-child {
        margin-top: 0; }

      &::before,
      &::after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        left: 50%;
        opacity: 0;
        z-index: 1;
        animation: {
          fill-mode: both;
          duration: 0.7s;
          timing-function: cubic-bezier(.9, -0.01, 1, .35); } }

      &.is-active {
        top: 0;
        opacity: 1; }

      &.is-correct {
        &::before {
          display: none; }

        &::after {
          width: 4em;
          height: 4em;
          border: 6px solid $border-red;
          border-radius: 50%;
          transform: translate(-50%, -50%) scale(1);
          opacity: 1;
          animation-name: scaleIn__Correct; } }

      &.is-incorrect {
        &::before {
          width: 4em;
          height: 1em;
          border: {
            bottom: 6px solid $border-blue;
            left: 6px solid $border-blue; }
          transform: translate(-50%, -50%) rotate(-40deg) scale(1);
          opacity: 1;
          animation-name: scaleIn__InCorrect; }

        &::after {
          display: none; } }

      &.is-excluded {
        &::before {
          display: none; }

        &::after {
          content: "採点対象外";
          width: 6em;
          line-height: 6em;
          color: $font-yellow;
          font-weight: bold;
          text-align: center;
          transform: translate(-50%, -50%) scale(1);
          border: 6px solid $border-yellow;
          opacity: 1;
          animation-name: scaleIn__Excluded; } } }

    $index-width: 3em;
    #{$root}--term {
      font-weight: bold;
      margin-top: 14px;
      padding-left: $index-width;

      &:first-child {
        margin-top: 0; } }

    #{$root}--label {
      &.is-index {
        width: $index-width;
        display: inline-block;
        text-align: left;
        text-indent: 0;
        margin-left: -#{$index-width}; } }

    #{$root}--description {
      margin-top: 10px;

      &:first-child {
        margin-top: 0; } } }

  @keyframes scaleIn__Excluded {
    0% {
      opacity: 0;
      filter: blur(6px);
      transform: translate(-50%, -50%) scale(3); }

    100% {
      opacity: 1;
      filter: blur(0);
      transform: translate(-50%, -50%) scale(1); } }

  @keyframes scaleIn__Correct {
    0% {
      opacity: 0;
      filter: blur(6px);
      transform: translate(-50%, -50%) scale(3); }

    100% {
      opacity: 1;
      filter: blur(0);
      transform: translate(-50%, -50%) scale(1); } }

  @keyframes scaleIn__InCorrect {
    0% {
      opacity: 0;
      filter: blur(6px);
      transform: translate(-50%, -50%) rotate(-40deg) scale(3); }

    100% {
      opacity: 1;
      filter: blur(0);
      transform: translate(-50%, -50%) rotate(-40deg) scale(1); } } }
