&__quiz {
  $root: &;

  &.is-text,
  &.is-textarea {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: $font-xx-large;

    #{$root}--item {
      line-height: 1.5;
      padding: 0.2em;
      transition: all 0.3s ease;

      &:focus {
        font-size: 3rem; } } }

  &.is-text {
    #{$root}--item {
      border: {
        top: none;
        left: none;
        right: none;
        bottom: 1px solid $border-black; }

      &:focus {
        border-bottom: 4px solid $border-black; } }

    &::after {
      content: attr(data-suffix);
 } }      // margin-left: 1em

  &.is-textarea {
    #{$root}--item {
      flex: 1;
      border: 1px solid $border-black;
      border-radius: $border-radius;

      &:focus {
        border: 4px solid $border-black; } } }

  $icon-size: 1em;
  $default-padding: 1em;
  $icon-margin: 0.4em;
  &.is-radio,
  &.is-checkbox {
    #{$root}--label {
      display: block;
      position: relative;
      font-size: $font-normal;
      padding: 0.5em $default-padding;
      padding-left: $icon-size + $default-padding + $icon-margin;
      background-color: rgba($bg-white, 1);
      border: 1px solid $border-x-dark-brown;
      border-radius: $border-radius; }

    &.is-active {
      #{$root}--label {
        background-color: rgba($bg-light-yellow, 1); } } }

  &.is-radio {
    #{$root}--item {
      width: $icon-size;
      height: $icon-size;
      display: inline-block;
      vertical-align: middle;
      margin: -2px $icon-margin 0 -#{$icon-size + $icon-margin};
      background-color: rgba($bg-white, 0);
      border: 1px solid $border-x-dark-brown;
      border-radius: 50%;
      cursor: pointer;

      &:checked {
        position: relative;

        &::after {
          content: '';
          width: 0.5em;
          height: 0.5em;
          display: block;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          background-color: rgba($border-x-dark-brown, 1);
          border-radius: 50%; } } } }

  &.is-checkbox {
    #{$root}--item {
      @include checkboxStyle;
      margin: {
        right: $icon-margin;
        left: -#{$icon-size + $icon-margin}; }
      cursor: pointer;

      &:checked {
        background-color: rgba($bg-black, 1); } } }

  @include breakpoint($max-media: tablet) {
    &.is-text,
    &.is-textarea {
      #{$root}--item {
        &:focus {
          font-size: $font-xx-large; } } }

    &.is-textarea {
      #{$root}--item {
        padding: 0.6em; } } }

  @include breakpoint($max-media: sp) {
    &.is-text,
    &.is-textarea {
      font-size: $font-normal;

      #{$root}--item {
        &:focus {
          font-size: $font-x-large; } } }

    &.is-text {
      #{$root}--item {
        border: {
          top: none;
          left: none;
          right: none;
          bottom: 1px solid $border-black; }

        &:focus {
          border-bottom: 4px solid $border-black; } } }

    &.is-textarea {
      #{$root}--item {
        padding: 0.6em;
        border: 1px solid $border-black;
        border-radius: $border-radius;

        &:focus {
          border: 4px solid $border-black; } } }

    &.is-radio,
    &.is-checkbox {
      #{$root}--label {
        font-size: $font-small; } } } }

