&__name {
  $root: &;

  &.is-quiz {
    width: 100%;
    max-width: 660px;
    margin: 20px auto 0;

    &:first-child {
      margin-top: 0; }

    #{$root}--unit {
      display: flex;
      justify-content: flex-end;
      font: {
        size: $font-small;
        family: 'Noto Sans JP', sans-serif;
        weight: 900; } }

    #{$root}--description {
      margin-left: 1em;

      &:first-child {
        margin-left: 0; } } } }
