&__quiz {
  $root: &;
  $margin-size: 40;
  $margin-percent: getRelativeValue($margin-size, $global-contents-width-pc);

  & &--items {
    display: flex;
    flex-wrap: wrap; }

  & &--item {
    margin: #{$margin-size}px 0 0 $margin-percent; }

  @for $i from 1 through 4 {
    &.is-col#{$i} {
      $item-width: ($global-contents-width-pc - $margin-size * ($i - 1)) / $i;
      #{$root}--item {
        width: getRelativeValue($item-width, $global-contents-width-pc);

        &:nth-child(-n+#{$i}) {
          margin-top: 0; }

        &:nth-child(#{$i}n+1) {
          margin-left: 0; } } } }

  @include breakpoint($max-media: phablet) {
    @for $i from 1 through 4 {
      &.is-col#{$i} {
        #{$root}--item {
          width: 100%;
          margin: {
            top: 20px;
            left: 0; }

          &:nth-child(-n+#{$i}) {
            margin-top: 20px; }

          &:first-child {
            margin-top: 0; } } } } } }
