&__questionAndAnswer {
  $root: &;

  &.is-tools {
    #{$root}--term {
      display: flex;
      align-items: center;
      justify-content: center;
      padding-top: 2em; }

    #{$root}--label {
      &.is-time {
        color: $font-black;
        font: {
          weight: normal;
          family: sans-serif; }
        padding: 0.1em 0.6em;
        background-color: rgba($bg-xx-light-gray, 1);
        border-radius: 4px; }

      &.is-question {
        margin-left: 1em; } }

    #{$root}--description {
      padding: {
        top: 3em;
        bottom: 3em; } } } }
