&__calendar {
  $root: &;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;

  &.is-saturday {
    #{$root}--item {
      &.is-date {
        color: $font-light-blue; } } }

  &.is-holiday,
  &.is-sunday {
    #{$root}--item {
      &.is-date {
        color: $font-light-red; } } }

  &.is-today {
    position: relative;
    z-index: 1;

    &::after {
      content: "";
      width: 0.3em;
      height: 0.3em;
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      background-color: rgba($bg-yellow, 1);
      border-radius: 50%; }

    #{$root}--item {
      &.is-date,
      &.is-event {
        color: $font-yellow; } } }

  & &--item {
    width: 100%;
    text-align: center;
    cursor: default;

    &.is-date {
      line-height: 1;
      font: {
        size: $font-x-large;
        weight: bold;
        family: Arial, sans-serif; }
      padding-top: 0.5em; }

    &.is-event {
      font-size: $font-xx-small;
      margin-top: 1em; } }

  @include breakpoint($max-media: tablet) {
    #{$root}--item {
      user-select: none; } }

  @include breakpoint {
    &.is-today {
      &::after {
        width: 0.2em;
        height: 0.2em; } } }

  @include breakpoint($max-media: sp) {
    &.is-today {
      &::after {
        top: -0.1em;
        left: -0.1em; } }

    #{$root}--item {
      &.is-date {
        font-size: $font-normal; } } } }

