&__slogan {
  $root: &;

  &.is-quiz {
    width: 100%;
    height: 100%;
    max-width: #{$global-contents-width-pc}px;
    margin: 0 auto;
    padding-top: #{$header-height-pc}px;

    #{$root}--item {
      width: 100%;
      height: 100%;
      color: rgba($font-white, 0);
      font-size: 0;
      background: {
        image: url(/nankyoku/assets/images/label_quiz_01.png);
        repeat: no-repeat;
        position: center top;
        size: contain; } }

    @include breakpoint($max-media: sp) {
      #{$root}--item {
        background-position: center center; } } } }
